body{padding:0;box-sizing:border-box;margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#15212b}#root{display:grid;grid-template-rows:8vh minmax(92vh, 92vh)}.grid{display:grid;grid-template-columns:-webkit-min-content auto;grid-template-columns:min-content auto;grid-template-rows:minmax(92vh, 92vh);max-height:92vh}section{max-height:92vh;overflow:auto;overflow-x:hidden;max-height:100%}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

header{width:100%;display:flex;height:8vh;flex-direction:row-reverse;background-color:#0D1321;justify-content:space-around}header img{overflow:hidden;margin:0 4%;max-height:80%;max-width:80%}header h1{box-sizing:content-box;text-align:center;align-self:center;margin-top:2%;display:block;width:20%;height:100%;text-transform:capitalize;color:#748CAB}@media screen and (max-width: 800px){header{width:100%;display:flex;height:8vh;flex-direction:row-reverse;background-color:#0D1321;justify-content:space-between}header img{overflow:hidden;margin:0 4%;max-height:80%;max-width:80%}header h1{font-size:medium;align-self:center;padding:2%;margin:0;margin-top:2%;display:block;width:20%;height:100%;align-content:center;text-transform:capitalize;color:#748CAB}}

body{padding:0;box-sizing:border-box;margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#15212b}#root{display:grid;grid-template-rows:8vh minmax(92vh, 92vh)}.grid{display:grid;grid-template-columns:-webkit-min-content auto;grid-template-columns:min-content auto;grid-template-rows:minmax(92vh, 92vh);max-height:92vh}section{max-height:92vh;overflow:auto;overflow-x:hidden;max-height:100%}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.contenedor-buscador{width:25%;display:flex;flex-flow:row-reverse;align-items:center;justify-content:flex-start}.contenedor-buscador button{background-color:transparent;border:none;height:50%;color:white;cursor:pointer}.contenedor-buscador input{transition:.8s;box-sizing:border-box;transition:.4s;height:50%;border:none;outline:none;border-bottom:3px solid white;width:15%;margin:1%;background-color:transparent;text-align:center;color:transparent}.contenedor-buscador input::-webkit-input-placeholder{-webkit-transition:all .4s;transition:all .4s;display:none;text-transform:capitalize;color:transparent;text-align:center}.contenedor-buscador input:-ms-input-placeholder{-ms-transition:all .4s;transition:all .4s;display:none;text-transform:capitalize;color:transparent;text-align:center}.contenedor-buscador input::-ms-input-placeholder{-ms-transition:all .4s;transition:all .4s;display:none;text-transform:capitalize;color:transparent;text-align:center}.contenedor-buscador input::placeholder{transition:all .4s;display:none;text-transform:capitalize;color:transparent;text-align:center}.contenedor-buscador input:focus{color:white;height:50%;border:none;outline:none;border-bottom:3px solid white;width:90%;margin:1%;background-color:transparent}.contenedor-buscador input:focus::-webkit-input-placeholder{color:white}.contenedor-buscador input:focus:-ms-input-placeholder{color:white}.contenedor-buscador input:focus::-ms-input-placeholder{color:white}.contenedor-buscador input:focus::placeholder{color:white}@media screen and (max-width: 800px){.contenedor-buscador input{transition:.8s;box-sizing:border-box;transition:.4s;height:50%;border:none;outline:none;border-bottom:3px solid white;width:100%;margin:1%;background-color:transparent;text-align:center;color:white}}

aside{margin:0 0;display:block;min-height:92vh;height:auto;width:-webkit-max-content;width:-moz-max-content;width:max-content;max-width:15vw;background-color:#0D1321;transition:all .5s}aside nav.escritorio{height:100%;background-color:transparent;display:flex;flex-direction:column;color:#fff}aside nav.escritorio h2{cursor:pointer;overflow:hidden;margin:0;box-sizing:border-box;text-align:center;padding-top:1em;border-bottom:3px solid black;padding-bottom:1em}

button.nav{cursor:pointer;padding:0;background-color:transparent;height:auto;overflow:hidden;font-family:sans-serif;font-size:0.8em;width:100%;background-color:#0D1321;font-weight:bold;border:none;transition:all .5s;text-overflow:ellipsis;padding:1.5em 0;display:flex;align-items:center;flex-flow:row;justify-content:center}button.nav span{text-transform:capitalize;color:#3E5C76;width:70%}button.nav svg{width:auto;font-size:1.5rem;color:#3E5C76;margin:0 1rem}button.nav .modal{display:none}button.nav:active{border:1px solid #38444d;outline:none}button.nav:hover{overflow:visible;margin-top:1.5%;width:105%;box-shadow:0 2px 3px black}button.nav:hover svg,button.nav:hover span{color:#42993d}

ul{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;position:relative;grid-gap:1em;gap:1em;grid-row-gap:4em;row-gap:4em}

.box{position:relative;height:300px;width:250px;background:#060c21;display:flex;justify-content:center;align-items:center;border:1px solid #000}.box:nth-child(odd) span{position:absolute;top:-2px;left:-2px;right:-2px;bottom:-2px;background:linear-gradient(-315deg, #42ff42, #a5f);z-index:-1;transform:skew(2deg, 2deg)}.box:nth-child(even) span{position:absolute;top:-2px;left:-2px;right:-2px;bottom:-2px;background:linear-gradient(-315deg, #f01, #fb4);z-index:-1;transform:skew(2deg, 2deg)}.box .content{position:absolute;padding:20px}.content h2{position:absolute;top:-20%;right:50px;max-width:100%;margin:0;padding:0;font-size:2em;color:rgba(255,255,255,0.05);transition:0.5s}.box:hover .content h2{top:-30%}.box .content h3{margin:0 0 10px;padding:0;font-size:2em;font-weight:500;color:#fff}.box .content p{margin:0;padding:0;font-size:0.8em;font-weight:500;color:#fff}

